$metrics-plugin-open-plugin-button-color: #aaaaaa;
$metrics-plugin-open-plugin-button-hovered-color: #999999;
$remove-showed-element-button-color: hsl(225, 10%, 85%);
$remove-showed-element-button-hovered-color: hsl(225, 10%, 75%);
$metricsplugin-container-border: lightgrey;
$metricsplugin-open-retrieve-removed-elements-button-color: #dddddd;
$metricsplugin-open-retrieve-removed-elements-button-hovered-color: var(
    --log-resizer-border-color
);
//var was log-resizer-border-color
//na ginontai pio skoura h pio light?
$metricsplugin-retrieve-removed-elements-button-color: hsl(225, 10%, 85%);
//prettier-ignore
$metricsplugin-retrieve-removed-elements-button-hovered-color: hsl(225,10%,75%);
$metrics-plugin-font-size: 1.2rem;
