.stats-table-elements-container:nth-child(3n + 0) {
    background: blue !important;
    width: 100% !important;
}

/* probably to metrics-container tha einai megalytero soon */

/* prosoxh edw */
.remove-element-button {
    /* font-size:1.2rem; */
    background-color: $remove-showed-element-button-color;
    width: 1.5rem;
    height: 1.5rem;
    border: solid 1px;
    border-radius: 0.125rem;
    &:hover {
        background-color: $remove-showed-element-button-hovered-color;
    }
}

.categories-wrapper {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    font-size: 1.25rem;
}
.categories-wrapper-title {
    border-bottom: solid 2px black;
    border-right: solid 2px black;
    border-left: solid 2px black;
    text-align: center;
}

.categories-wrapper-children {
    display: flex;
}

.tools-container {
    flex: 0.75;
    padding-left: 1rem;
    .addremoved-widget-subtitle {
        font-size: $metrics-plugin-font-size;
    }
    .addremoved-widget-button {
        padding: 8px;
        background-color: $metricsplugin-open-retrieve-removed-elements-button-color;
        &:hover {
            background-color: $metricsplugin-open-retrieve-removed-elements-button-hovered-color;
        }
    }
    .addremoved-menu {
        margin-top: 1rem;
        border: solid 2px black;
        display: flex;
        flex-direction: column;
        height: 170px;
        overflow: auto;
        padding-left: 1rem;
        padding-bottom: 1rem;
    }
    .addremoved-okbutton {
        margin-top: auto;
        background-color: $metricsplugin-retrieve-removed-elements-button-color;
        /* TODO na ginei alliws auto */
        width: 50px;
        padding: 4px;
        margin-left: auto;
        margin-right: 1rem;
        border-radius: 8px;
        &:hover {
            background-color: $metricsplugin-retrieve-removed-elements-button-hovered-color;
        }
    }
    .addremoved-widget-element-component {
        display: flex;
        align-items: center;
        column-gap: 0.5rem;
        .addremoved-widget-element-name {
            font-size: $metrics-plugin-font-size;
        }
        .addremoved-widget-element-checkbox {
            font-size: $metrics-plugin-font-size;
            accent-color: #aaaaaa;
        }
        .addremoved-widget-element-checkbox:checked {
            background-color: #999999;
            color: #999999;
        }
    }
}
